*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
}
.parent {
  width: 900px;
  height: 500px;
  border: 1px solid #000;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  font-size: 25px;
  position: relative;
}
.side {
  width: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.side > .player-0-score,
.side > .player-1-score {
  transform: translate(0, -120px);
}
.player-0-name > span,
.player-1-name > span {
  position: relative;
}

.active {
  background-color: rgb(30, 255, 199);
}
.active > .player-0-name > span::after,
.active > .player-1-name > span::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: red;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(10px, -50%);
  border-radius: 50%;
}

button {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid black;
  cursor: pointer;
  font-size: 25px;
  color: black;
  outline: 0;
  padding: 0.4rem;
  border-radius: 5px;
  background-color: rgba(255, 30, 236, 0.781);
}
.new {
  top: 40px;
}
.role {
  top: 350px;
}
.hold {
  top: 430px;
}
.img-dice {
  width: 100px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 220px;
  border: 1px solid #000;
}

#winner {
  background-color: #0a77b0;
  color: #fff;
}
@media (max-width: 950px) and (min-width: 750px) {
  .parent {
    width: 700px;
  }
}
@media (max-width: 749px) and (min-width: 650px) {
  .parent {
    width: 600px;
  }
}
@media (max-width: 649px) and (min-width: 550px) {
  .parent {
    width: 500px;
  }
  button {
    font-size: 20px;
  }
  .img-dice {
    width: 80px;
  }
}
@media (max-width: 549px) and (min-width: 450px) {
  .parent {
    width: 450px;
  }
  button {
    font-size: 17px;
  }
  .img-dice {
    width: 80px;
  }
  .side > .player-0-score,
  .side > .player-1-score,
  .player-0-name > span,
  .player-1-name > span,
  .player-0-sum,
  .player-1-sum {
    font-size: 1.5rem;
  }
}
@media (max-width: 449px) {
  .parent {
    width: 350px;
  }
  button {
    font-size: 17px;
  }
  .img-dice {
    width: 80px;
  }
  .side > .player-0-score,
  .side > .player-1-score,
  .player-0-name > span,
  .player-1-name > span,
  .player-0-sum,
  .player-1-sum {
    font-size: 1.3rem;
  }
}
